<template>
  <div class="wrap pt1">
    <div class="course-content">
      <div>
        <filter-bar
          name="weekFilter"
          showBuild
          showFloor
          showClsRoom
          is-rec-plan
          :deviceType="1"
          :pTypeList="pTypeList"
          :lessonList="lessonList"
          :gradeList="gradeList"
          :teacherList="teacherList"
          :schoolList="schoolList"
          :buildingList="buildingList"
          :floorList="floorList"
          :clsroomList="clsroomList"
          :isRecPlan="false"
          @fetchData="filterFetch"
          @fetch="filter"
          ref="filerbar"
        ></filter-bar>
      </div>
      <div class="courses-box" id="roomPanel">
        <select-container
          ref="selectContainer"
          v-loading="loading"
          :course-list="courseList"
          :page-size="pageSize"
          :total="allItems"
          :islive="isLive"
          @pageChange="fetchData"
        ></select-container>
        <side-recommend title="推荐精品" :isLive="isLive"></side-recommend>
      </div>
    </div>
    <footer-infor></footer-infor>
  </div>
</template>
<script>
import filterBar from "../../components/filterBar";
import http from "../../utils/http";
import api from "../../utils/api";
// import bigBanner from "../../components/bigBanner";
import selectBox from "../../components/courseSelect";
import selectContainer from "../../components/selectContainer.vue";
import sideRecommend from "../../components/sideRecommend";
import footerInfor from "../../components/footerInfor";
import { formatDate } from "../../utils/utils.js";
export default {
  components: {
    // bigBanner,
    selectBox,
    selectContainer,
    sideRecommend,
    footerInfor,
    filterBar
  },
  data() {
    return {
      isLive: false,
      key: "",
      sdir: 1,
      loading: true,
      sfield: "",
      pageSize: http.pageSize,
      allItems: 0,
      // bannerImg: "../../../static/jpkc.png",
      courseList: [],
      time: "",
      dept: "",
      cids: "",
      bids: "",
      fs: "",
      pt: false,
      less: "",
      zynj: "",
      clsid: "",
      pTypeList: [
        {
          text: "录制",
          code: "pt", // 参数名称
          id: "1" // 参数值
        },
        {
          text: "直播",
          code: "pt", // 参数名称
          id: "2" // 参数值
        }
      ],
      teachers: "",
      lessonList: [],
      gradeList: [],
      teacherList: [],
      schoolList: [],
      buildingList: [],
      floorList: [],
      clsroomList: []
    };
  },
  watch: {
    $route(to, from) {
      this.getRouteDatas();
    }
  },
  mounted() {
    console.log(122);
    // this.fetchBanner();
    this.getRouteDatas();
    // this.LoadScreen();
  },
  methods: {
    filterFetch(val) {},
    filter(val) {
      let cidstemp = "";
      let bidstemp = "";
      let fstemp = "";
      let ptemp = "";
      let lesstemp = "";
      let zynjtemp = "";
      let teachertemp = "";
      let clsidtemp = "";
      val.forEach(element => {
        switch (element.code) {
          case "cids":
            cidstemp += element.id + ",";
            break;
          case "bids":
            bidstemp += element.id + ",";
            break;
          case "fs":
            fstemp += element.id + ",";
            break;
          case "clsids":
            clsidtemp += element.id + ",";
            break;
          case "pt":
            ptemp += element.id + ",";
            break;
          case "less":
            lesstemp += "'" + element.id + "',";
            break;
          case "zynj":
            zynjtemp += "'" + element.id + "',";
            break;
          case "teach":
            teachertemp += element.id + ",";
            break;
          default:
            break;
        }
      });
      this.cids = cidstemp.slice(0, cidstemp.length - 1);
      this.bids = bidstemp.slice(0, bidstemp.length - 1);
      this.clsid = clsidtemp.slice(0, clsidtemp.length - 1);
      this.fs = fstemp.slice(0, fstemp.length - 1);
      this.pt = ptemp.slice(0, ptemp.length - 1);
      this.less = lesstemp.slice(0, lesstemp.length - 1);
      this.zynj = zynjtemp.slice(0, zynjtemp.length - 1);
      this.teachers = teachertemp.slice(0, teachertemp.length - 1);
      this.LoadScreen();
    },
    LoadScreen() {
      this.fetchfilterData([
        api.getAllCampusesData,
        {
          token: http.getToken()
        },
        0
      ]);
      //大楼
      this.fetchfilterData([
        api.getBuildingDatasByCT,
        {
          token: http.getToken(),
          cids: this.cids,
          type: 1
        },
        1
      ]);
      //楼层
      this.fetchfilterData([
        api.getFloorByBuilding,
        {
          token: http.getToken(),
          cids: this.cids,
          bids: this.bids
        },
        2
      ]);
      //课程、专业、教师
      this.fetchfilterData([
        api.GetRecScreenCondition,
        {
          date: "1",
          cids: this.cids,
          bids: this.bids,
          fs: this.fs,
          token: http.getToken()
        },
        3
      ]);
      //教室
      this.fetchfilterData([
        api.getDatasByCampusBuildingFloors,
        {
          cids: this.cids,
          bids: this.bids,
          floors: this.fs,
          type: 9,
          token: http.getToken()
        },
        4
      ]);
      //数据
      this.fetchByLastPage();
    },
    fetchfilterData: async function(paramsArr) {
      let url = paramsArr[0];
      let params = paramsArr[1];
      let type = paramsArr[2];
      let res = await http.get(url, params);
      if (res.Datas) {
        switch (type) {
          case 0:
            //校区
            if (res.Datas.length == 1) {
              this.isSchoolHide = true;
            } else {
              this.schoolList = [];
              res.Datas.forEach(element => {
                this.schoolList.push({
                  text: element.CampusesName,
                  code: "cids",
                  id: element.CampusesID
                });
              });
            }
            break;
          case 1:
            //教学楼
            this.buildingList = [];
            res.Datas.forEach(element => {
              this.buildingList.push({
                text: element.BuildingName,
                code: "bids",
                id: element.BuildingID
              });
            });
            break;
          case 2:
            //楼层
            this.floorList = [];
            res.Datas.forEach(element => {
              this.floorList.push({
                text: "第" + element + "层",
                code: "fs",
                id: element
              });
            });
            break;
          case 4:
            //教室
            this.clsroomList = [];
            res.Datas.forEach(element => {
              this.clsroomList.push({
                text: element.ClsRoomName,
                code: "clsids",
                id: element.ClsRoomID
              });
            });
            break;
          case 3:
            //专业、课程
            this.lessonList = [];
            this.gradeList = [];
            this.teacherList = [];
            if (res.Datas.Title) {
              res.Datas.Title.forEach(element => {
                this.lessonList.push({
                  text: element,
                  code: "less",
                  id: element
                });
              });
            }
            if (res.Datas.ZYNJ) {
              res.Datas.ZYNJ.forEach(element => {
                this.gradeList.push({
                  text: element,
                  code: "zynj",
                  id: element
                });
              });
            }
            if (res.Datas.Teachers) {
              res.Datas.Teachers.forEach(element => {
                this.teacherList.push({
                  text: element,
                  code: "teach",
                  id: element
                });
              });
            }
            break;
          default:
            break;
        }
      }
    },
    //传递过来选中的参数
    getRouteDatas() {
      if (this.$route.query.selectData) {
        let receivearray = JSON.parse(this.$route.query.selectData);
        receivearray.forEach(element => {
          if (element.title === "搜索") {
            this.key = element.text;
          }
        });
      }
      this.fetchByLastPage();
    },
    fetchData(val = 0) {
      let params = {
        key: this.key,
        date: "",
        cids: this.cids,
        bids: this.bids,
        floors: this.fs,
        isRec: this.pt,
        isLive: this.pt,
        less: this.less,
        gras: this.zynj,
        dept: this.dept,
        clsrooms: this.clsid,
        starttime: formatDate(this.$refs.filerbar.dataRange[0], "yy-MM-dd"),
        endtime: formatDate(this.$refs.filerbar.dataRange[1], "yy-MM-dd"),
        sfield: "m.MaterialCreateDate",
        sdir: this.sdir,
        teachers: this.teachers,
        psize: 36,
        pno: val
      };
      this.fetchCourse(params);
    },
    async fetchCourse(params) {
      const res = await http.get(api.getMaterialInfoBycondition, params);
      this.loading = false;
      this.courseList = [];
      if (res.Result) {
        let downloadURL = await api.GetDownloadFileURL();
        console.log(downloadURL);
        res.Datas.forEach(element => {
          let ScheduleInfo = {};

          try {
            if (element.ScheduleInfo)
              ScheduleInfo = JSON.parse(element.ScheduleInfo);
          } catch (e) {}
          this.courseList.push({
            title:
              ScheduleInfo && ScheduleInfo.CourseName
                ? ScheduleInfo.CourseName
                : element.RTName,
            imgSrc: downloadURL + "?filename=" + element.RTID + "&fileType=102",
            label: element.RTName,
            recid: element.RTID,
            teacher: ScheduleInfo.Teachers,
            id: element.MaterialID,
            watcher:
              element.Visits && element.Visits == "" ? 0 : element.Visits,
            lesson:
              element.RTStart && element.RTEnd
                ? formatDate(element.RTStart, "hh:mm") +
                  "-" +
                  formatDate(element.RTEnd, "hh:mm")
                : "",
            class: ScheduleInfo.Classes,
            date: formatDate(
              new Date(element.MaterialCreateDate),
              "yyyy-MM-dd"
            ),
            roomname: element.ClsRoomName
          });
        });
      }
      this.allItems = res.RowsCount;
      this.pageSize = res.PageSize;
      this.$refs.selectContainer.selectCurrentPage = res.PageIndex
        ? res.PageIndex
        : 1;
    },
    fetchByLastPage() {
      let tempPageIndex = 1;
      if (window.sessionStorage.getItem("FJRH_course_pno")) {
        tempPageIndex = window.sessionStorage.getItem("FJRH_course_pno");
      }
      this.fetchData(tempPageIndex);
    }
  }
};
</script>
<style lang="less" scoped></style>



